    width: 1280px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.section {
  padding-left: 0;
  padding-right: 0;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

:root {
  --main-font: Roboto, sans-serif;
  --secondary-font: Kalam, sans-serif;
  --main-txt-color: #2c2c2c;
  --secondary-txt-color: #7eb101;
  --accent-bg-color: #bbf330;
  --accent-bg-color-hover: #b5ff00;
  --main-bg-color: #fefefe;
  --main-border-color: #ffe550;
  --animations: 250ms cubic-bezier(.4, 0, .2, 1);
  --placeholder-txt-color: #505050;
  --fruit-gradient: radial-gradient( 50% 50% at 50% 50%, #2f583f 0%, rgba(105, 105, 105, 0) 100% );
}

